/* eslint-disable react/destructuring-assignment, react/prop-types */
import React from 'react';
import { createUseStyles } from 'react-jss';

import { useEpisode } from './Context';

const useStyles = createUseStyles({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  button: ({ progress, size }) => ({
    transition: 'opacity 218ms linear',
    opacity: progress >= size - 1 ? 1 : 0,
    pointerEvents: progress >= size - 1 ? 'all' : 'none',
  }),
}, { name: 'EpisodeChoices' });

const Choices = (props) => {
  const episode = useEpisode();
  const classes = useStyles({ ...props, ...episode });

  return (
    <div className={[props.className, classes.root].filter(Boolean).join(' ')}>
      {episode.step.choices.map(({ target, title }) => (
        <button
          className={classes.button}
          type="button"
          key={target}
          onClick={() => episode.onMakeChoice(target)}
        >
          {title}
        </button>
      ))}
    </div>
  );
};

export default Choices;
