/* eslint-disable react/prop-types, react/destructuring-assignment, react/jsx-props-no-spreading */
import React from 'react';
import { graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { createUseStyles } from 'react-jss';

import Layout from '../Layout/Layout';
import EpisodeProvider, { useEpisode } from './Context';
import Choices from './Choices';

const useStyles = createUseStyles({
  root: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },

  body: {
    margin: 'auto',
  },

  choices: {},
}, { name: 'Episode' });

const Episode = () => {
  const episode = useEpisode();
  const classes = useStyles(episode);
  const { step: { body } } = episode;

  return (
    <div className={classes.root}>
      <div className={classes.body}>
        <MDXRenderer>{body}</MDXRenderer>
      </div>

      <Choices className={classes.choices} />
    </div>
  );
};

const EpisodePage = (props) => (
  <EpisodeProvider data={props?.data?.episodeMdx}>
    <Layout>
      <Episode />
    </Layout>
  </EpisodeProvider>
);

EpisodePage.displayName = 'MKEpisode';

export default EpisodePage;

export const query = graphql`
query allEpisodeMdx ($seasonNr: Float = 0, $episodeNr: Float = 0) {
  episodeMdx: allMdx(filter: {fields: {seasonNr: {eq: $seasonNr}, episodeNr: {eq: $episodeNr}}}) {
    nodes {
      parent {
        ... on File {
          name
        }
      }
      frontmatter {
        title
        choices {
          target
          title
        }
      }
      body
    }
  }
}
`;
